'use client';

import React, { useEffect, useState } from 'react';

// shadcn/ui component imports
import {
  Accordion,
  AccordionContent,
  AccordionItem,
  AccordionTrigger,
} from '@/components/ui/accordion';
import { Button } from '@/components/ui/button';
import { Card, CardContent } from '@/components/ui/card';
import { Input } from '@/components/ui/input';
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from '@/components/ui/select';

// Custom hook and type imports
import { useDebounce } from '@/lib/hooks';
import { Category } from '../types.d';
import ErrorDisplay from './_components/ErrorDisplay';
import QuestionItem from './_components/QuestionItem';

export default function HomePage() {
  const [categories, setCategories] = useState<Category[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  // State for filters and pagination
  const [searchTerm, setSearchTerm] = useState('');
  const [difficulty, setDifficulty] = useState('all');
  const [page, setPage] = useState(1);
  const [pagination, setPagination] = useState({
    page: 1,
    limit: 10,
    totalPages: 1,
    totalResults: 0,
  });

  // Debounce the search term to avoid excessive API calls
  const debouncedSearchTerm = useDebounce(searchTerm, 300); // 300ms delay

  useEffect(() => {
    const fetchData = async () => {
      setLoading(true);
      setError(null);

      // Construct the API URL with query parameters
      const params = new URLSearchParams();
      if (debouncedSearchTerm) params.append('search', debouncedSearchTerm);
      if (difficulty !== 'all') params.append('difficulty', difficulty);
      params.append('page', page.toString());
      params.append('limit', '10'); // Or your desired limit

      try {
        const response = await fetch(
          `http://localhost:5000/api/v1/content?${params.toString()}`
        );
        if (!response.ok) {
          throw new Error(`HTTP error! Status: ${response.status}`);
        }
        const result = await response.json();
        setCategories(result.data);
        setPagination(result.pagination);
      } catch (err: unknown) {
        if (err instanceof Error) setError(err.message);
        else setError('An unexpected error occurred.');
      } finally {
        setLoading(false);
      }
    };

    fetchData();
  }, [debouncedSearchTerm, difficulty, page]); // Re-fetch when these change

  const handleDifficultyChange = (value: string) => {
    setDifficulty(value);
    setPage(1); // Reset to first page on filter change
  };

  const handleSearchChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    setSearchTerm(event.target.value);
    setPage(1); // Reset to first page on search change
  };

  if (error) return <ErrorDisplay message={error} />;

  return (
    <main className="min-h-screen bg-background text-foreground font-sans p-4 sm:p-6 md:p-8">
      <div className="max-w-4xl mx-auto">
        <header className="text-center mb-12">
          <h1 className="text-4xl md:text-5xl font-extrabold tracking-tight">
            GeekHeaven DSA Sheet
          </h1>
          <p className="text-muted-foreground mt-3 text-lg">
            Your curated list of essential DSA problems.
          </p>
        </header>

        {/* Filter and Search Bar */}
        <Card className="mb-8">
          <CardContent className="p-4 flex flex-col sm:flex-row gap-4">
            <Input
              placeholder="Search for a question..."
              value={searchTerm}
              onChange={handleSearchChange}
              className="flex-grow"
            />
            <Select value={difficulty} onValueChange={handleDifficultyChange}>
              <SelectTrigger className="w-full sm:w-[180px]">
                <SelectValue placeholder="Filter by difficulty" />
              </SelectTrigger>
              <SelectContent>
                <SelectItem value="all">All Difficulties</SelectItem>
                <SelectItem value="Easy">Easy</SelectItem>
                <SelectItem value="Medium">Medium</SelectItem>
                <SelectItem value="Hard">Hard</SelectItem>
              </SelectContent>
            </Select>
          </CardContent>
        </Card>

        {/* Content Display */}
        {loading ? (
          <div className="text-center py-16">
            <div className="animate-spin rounded-full h-16 w-16 border-t-2 border-b-2 border-primary mx-auto"></div>
            <p className="mt-4 text-muted-foreground">Loading questions...</p>
          </div>
        ) : categories.length > 0 ? (
          <Accordion type="single" collapsible className="w-full">
            {categories.map((category) => (
              <AccordionItem value={category._id} key={category._id}>
                <AccordionTrigger className="text-xl hover:no-underline text-primary">
                  {category.title}
                </AccordionTrigger>
                <AccordionContent>
                  <div className="space-y-3 pt-2">
                    {category.questions.map((question) => (
                      <QuestionItem key={question._id} question={question} />
                    ))}
                  </div>
                </AccordionContent>
              </AccordionItem>
            ))}
          </Accordion>
        ) : (
          <div className="text-center py-16">
            <h3 className="text-2xl font-semibold">No Questions Found</h3>
            <p className="text-muted-foreground mt-2">
              Try adjusting your search or filter criteria.
            </p>
          </div>
        )}

        {/* Pagination Controls */}
        {!loading && pagination.totalPages > 1 && (
          <div className="flex items-center justify-between mt-8">
            <Button onClick={() => setPage(page - 1)} disabled={page <= 1}>
              Previous
            </Button>
            <span className="text-muted-foreground">
              Page {pagination.page} of {pagination.totalPages}
            </span>
            <Button
              onClick={() => setPage(page + 1)}
              disabled={page >= pagination.totalPages}
            >
              Next
            </Button>
          </div>
        )}

        <footer className="text-center mt-16 text-muted-foreground">
          <p>Built for the WebD Selection Task 2025-26.</p>
        </footer>
      </div>
    </main>
  );
}
